import React from 'react';
import VideoLink from './VideoLink';

class Container extends React.Component {
	constructor() {
		super();
		this.state = {
			videos : [
				{
					url : "http://img.youtube.com/vi/S1Br9SRv3EA/0.jpg",
					author : "Auteur de la video",
					title : "Titre de la video"
				},
				{
					url : "http://img.youtube.com/vi/S1Br9SRv3EA/0.jpg",
					author : "Auteur de la video2",
					title : "Titre de la video2"
				}
			]
		};
	}
	render() {
		const videoLinks = this.state.videos.map((videoDesc, index) =>
			<VideoLink 
				url={videoDesc.url}
				title={videoDesc.title}
				author={videoDesc.author}
			/>
		);
		return (
			<div className="container">
				<div className='row'>
					<div className="col-lg-12">
						<div id="videos-container">
							{videoLinks}
						</div>
					</div>
				</div>
			</div>
		);
	}
}

export default Container;


